import { Component, OnInit } from '@angular/core';
import { ListOfCountryPage } from '../list-of-country/list-of-country.page';
import { NavController, ModalController } from '@ionic/angular';

@Component({
  selector: 'app-country-of-citizenship',
  templateUrl: './country-of-citizenship.page.html',
  styleUrls: ['./country-of-citizenship.page.scss'],
})
export class CountryOfCitizenshipPage implements OnInit {

  constructor(public navCtrl: NavController,public model:ModalController) { }

  ngOnInit() {
  }

  async openModel(){
    const myModel =await this.model.create({
      component: ListOfCountryPage,
      cssClass: 'country-modal-css'
    });
 
    await myModel.present();
   }

   goToPremierChecking() {
    this.navCtrl.navigateForward("premier-checking");
  }

  goBack() {
    this.navCtrl.navigateForward("social-security-number");
  }

}
